.user {
    width: 85%;
    padding: 20px;

    .userTitleContainer {
        display: flex;
        align-items: center;
        justify-content: space-between;
        // .userTitle {

        // }
        .userAddButton {
            width: 80px;
            border: none;
            padding: 5px;
            background-color: teal;
            cursor: pointer;
            color: #fff;
            font-size: 1rem;
            border-radius: 5px;

            &:hover {
                background: rgb(45, 145, 145);
            }
        }
    }

    .userContainer {
        display: flex;
        margin-top: 20px;
        gap: 20px;
        .userShow {
            width: 33%;
            padding: 20px;
            box-shadow:  0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05);

            .userShowTop {
                display: flex;
                align-items: center;
                gap: 20px;

                .userShowImg {
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    object-fit: cover;
                }
                .userShowTopTitle {
                    display: flex;
                    flex-direction: column;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    .userShowUsername {
                        font-weight: 600;
                    }
                    .userShowEmail {
                        font-weight: 300;
                    }
                }
            }
            @media screen and (max-width:900px){
                width: 50%;
            }
            @media screen and (max-width:500px){
                width: 100%;
            }
        }
        .userUpdate {
            width: 67%;
            padding: 20px;
            box-shadow:  0 10px 15px -3px rgba(0,0,0,0.1),0 4px 6px -2px rgba(0,0,0,0.05);

            .userUpdateTitle {
                font-size: 24px;
                font-weight: 600;
            }
            .userUpdateForm {
                display: flex;
                justify-content: space-between;
                margin-top: 20px;
                width: 100%;
                .userUpdateLeft {
                    width: 50%;
                    .userUpdateItem {
                        display: flex;
                        flex-direction: column;

                        .userUpdateInput {
                            width: 100%;
                            border: none;
                            border-bottom: 1px solid gray;
                            margin-bottom: 10px;
                            font-size: 1rem;
                        }
                    }
                }

                .userUpdateRight {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    //width: 50%;
                    text-align: end;
                    .userUpdateUpload {
                        display: flex;
                        align-items: center;
                        .userUpdateImg {
                            width: 100px;
                            height: 100px;
                            border-radius: 10px;
                            object-fit: cover;
                            margin-right: 20px;
                        }
                        label {
                            svg {
                                font-size: 1.4rem;
                                cursor: pointer;
                            }
                        }
                    }
                    .userUpdateButton {
                        cursor: pointer;
                        border-radius: 5px;
                        border: none;
                        background-color: rgb(59, 59, 195);
                        color: #ffff;
                        padding: 10px 10px;
                        font-weight: 600;
                        &:hover {
                            background-color: rgb(80, 80, 210);
                        }
                    }

                    button:disabled {
                        opacity: 0.65;
                        cursor: progress;
                    }
            
                    .error {
                        margin-top: 15px;
                        padding: 3px 10px;
                        border-radius: 5px;
                        border: 1px solid rgb(208, 41, 41);
                        background: rgb(245, 230, 230);
                        p {
                            color: rgb(208, 41, 41);
                            text-align: center;
                            font-weight: 500;
                        }
                    }
                    .userSuccess {
                        margin-top: 15px;
                        padding: 3px 10px;
                        border-radius: 5px;
                        border: 1px solid green;
                        background: rgb(228, 240, 228);
                        p {
                            color: green;
                            text-align: center;
                            font-weight: 500;
                        }
                    }
                }
                
                @media screen and (max-width:480px){
                    flex-direction: column;
                    gap: 20px;

                    .userUpdateLeft {
                        width: 100%;
                    }
                    .userUpdateRight {
                        width: 100%;

                        .userUpdateButton {
                            margin: 20px 0;
                        }
                    }
                }
            }

            @media screen and (max-width:900px){
                width: 100%;
            }
        }

        @media screen and (max-width:900px){
            flex-direction: column;
        }
    }

    @media screen and (max-width:900px){
        width: 100%;
    }
}
label {
    margin-bottom: 15px;
    font-weight: 500;
}
input:focus, textarea:focus, select:focus{
    outline: none;
}
.radioHolder {
    display: flex;
    gap: 10px;

    span {

        label {
            color: black;
            margin-right: 5px;
        }
    }
}